(function(win) {
        var docEl = win.document.documentElement;
        var time;

        function refreshRem() {
                var width = docEl.getBoundingClientRect().width;
                if (width > 768) { // 
                        width = 768;
                }
                var rem = width / 375*50;
                docEl.style.fontSize = rem + 'px';
                ///remfont-size:50px

        }

        win.addEventListener('resize', function() {
                clearTimeout(time);
                time = setTimeout(refreshRem, 1);
        }, false);
        win.addEventListener('pageshow', function(e) {
                if (e.persisted) {
                        clearTimeout(time);
                        time = setTimeout(refreshRem, 1);
                }
        }, false);
        //   rem

        refreshRem();

})(window);
//getBoundingClientRect()Object,6 top bottomrightleftwidthheight
//getBoundingClientRect()IEFF3+opera9.5+safari 4
